@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
html {
  background-color: #15181C;
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  margin: 0;
}

button {
  border: 0;
  border-radius: .25rem;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  min-width: 114px;
  padding: 5px;
}

p {
  margin: 0;
}

::-webkit-input-placeholder {
  color: white;
}

:-ms-input-placeholder {
  color: white;
}

::placeholder {
  color: white;
}

.countdown-container {
  /**
    * Styles repeated in idea
    */
  background-color: #000000;
  border: 1px solid #282828;
  border-radius: 10px;
  box-sizing: border-box;
  margin-top: 10px;
  padding: 10px;
}
.idea {
  /**
   * Next three styles are repeated in welcome
   * container
   */
  background-color: #000000;
  border: 1px solid #282828;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 10px;
}

.idea-section-header {
  font-weight: bold;
}

.idea-section-text {
  margin-top: 10px;
}

.idea-upvote-button {
  cursor: pointer;
}

.idea-upvote-button-container {
  -webkit-align-content: center;
          align-content: center;
  display: -webkit-flex;
  display: flex;
}

.idea-upvote-count {
  line-height: 30px;
  margin-left: 5px;
}

.idea-section,
.idea-upvote-button-container {
  margin-top: 20px;
}
.close-modal-button {
  cursor: pointer;
}

.close-modal-button-container {
  text-align: right;
}

.modal-content-body-text-bold {
  font-weight: bold;
}

.modal-content-container {
  padding: 10px;
}

.modal-content-container li {
  margin-bottom: 5px;
}

.modal-header {
  text-align: center;
}

.ReactModal__Content.ReactModal__Content--after-open {
  margin: 0 auto;
  max-width: 1000px;
}

.modal-content-body-text,
.modal-content-bullet-points,
.modal-header,
.modal-sub-header {
  margin-bottom: 10px;
}

@media only screen and (min-width: 950px) {
  .ReactModal__Overlay.ReactModal__Overlay--after-open {
    display: block;
  }
}
.secondary-navigation-button {
  border-radius: 50%;
  display: block;
  height: 50px;
  min-width: 50px;
  width: 50px;
}

.secondary-navigation-button-create-idea {
  background-color: #1DA1F2;
  margin-top: 10px;
}

.secondary-navigation-button-help {
  background-color: #15181C;
}
.form-control {
  background-color: #15181C;
  border: 1px solid #282828;
  box-sizing: border-box;
  color: #ffffff;
  display: block;
  padding: 10px;
  width: 100%;
}

.form-control-character-count {
  margin-top: 10px;
  text-align: right;
}

.text-area {
  height: 200px;
  resize: none;
}

.text-input {
  height: 50px;
}

.text-input-container {
  margin-bottom: 20px;
}

.text-input-error {
  color: red;
}

.text-input-error,
.text-input-label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}
.share-idea-modal-row-button {
  text-align: center;
}

.share-idea-modal-submit-button {
  background-color: #1DA1F2;
  height: 50px;
  width: 100%;
}

.share-link-button {
  background-color: transparent;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 120px;
}

.share-link-button-text {
  font: 1.8rem 'Roboto', sans-serif;
}

.share-link-buttons-container {
  display: -webkit-flex;
  display: flex;
}

.share-link-button-icon {
  cursor: pointer;
}

.share-link-button-second {
  margin-left: 30px;
}

.share-link-text-input {
  background-color: #15181C;
  border: 1px solid #282828;
  box-sizing: border-box;
  color: #ffffff;
  display: block;
  height: 30px;
  padding: 10px;
  width: 100%;
}

.share-link-button-icon,
.share-link-button-text {
  text-align: center;
}
.welcome-container {
  border: 1px solid #282828;
  border-radius: 10px;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px;
}

.welcome-container-button {
  height: 50px;
  width: 120px;
}

.welcome-container-button-share {
  background-color: #1DA1F2;
  margin-left: 20px;
}

.welcome-container-button-start {
  border: 1px solid #1DA1F2;
  color: #1DA1F2;
}

.welcome-container-buttons-container {
  margin-top: 10px;
}

.welcome-container,
.welcome-container-button-start {
  background-color: #000000;
}

.app {
  margin: 0 auto;
  max-width: 700px;
  padding: 10px;
}

.header-2-bold {
  font-size: 2.4rem;
  font-weight: bold;
}

.header-3-bold {
  font-size: 2rem;
  font-weight: bold;
}

.ideas {
  margin-top: 30px;
}

.ideas > div {
  margin-top: 10px;
}

.secondary-navigation {
  position: fixed;
  bottom: 10px;
  right: 10px;
}
